.box-with-border,
.box {
  width: 28%;
  height: 100px;
  background: #4fade3;
  margin: 24px;
  display: inline-block;
  position: relative;
}

.box-with-border span,
.box span {
  color: #f1f1f1;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 100%;
  left: 0;
  text-align: center;
  font-size: 18px;
}

.box-with-border {
  border: 4px solid #4fade3;
  background: #eee;
}

.box-with-border span {
  color: #4fade3;
}

.box.arrow-top-left:after {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  visibility: visible;
}

.box.arrow-top-left:after {
  border-width: 10px;
  border-bottom-color: #4fade3;
  top: -20px;
  left: 10px;
}

