.center-holder {
  position: relative;
  height: 300px;
  border: 1px solid #000;
}

.center-holder .centered {
  background-color: aqua;
  color: aliceblue;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
